.body{
    background: linear-gradient(135deg, #b5fff9fd, #4ad8cc);
    color: #ffffff;
    font-family:'cursive';
    margin: 0px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    height: 100vh;
    overflow: hidden;
}
.calculator{
    background: rgb(50, 148, 155);
    border: 8px solid #1e7c88;
    padding:30px;
    box-shadow:0 0 30px rgb(59, 161, 168);
    width:500px;
    text-align: center;
    backdrop-filter: blur(10px);
    margin: 20px;
    position:relative;
    overflow: hidden;

}

.display{
    width: 95%;
    height: 80px;
    margin-bottom: 30px;
    padding: 15px;
    font-size: 32px;
    text-align: right;
    border: 2px solid rgb(74, 222, 233);
    border: radius 25px;
    background: rgba(54, 230, 191);
    color: #2f8f87;

}
.buttons{
    display:grid;
    grid-template-columns: repeat(5,1fr);
    gap: 15px;
}
button{
    height: 60px;
    font-size: 18px;
    border: none;
    border-radius: 50px;
    background: linear-gradient(135deg, #92e6f1, #2f8f87);
    cursor: pointer;
    transition: all 0.3 ease;
    position:relative;
    overflow:hidden;
}
button:hover{
    background: linear-gradient(135deg #2c6670, #84c1c9);
    box-shadow: 0 0 20px rgb(14, 45, 49);
    transform:scale(1.1);
}
button:active{
    transform: scale(0.95);
    box-shadow: 0 10px 10px rgb(66, 152, 158);
}
#backspace{
    background:linear-gradient(135deg, #1a664f, #1eb191);
    color: #fff;
}
#backspace:hover{
    background:linear-gradient(135deg, #1eb191, #1a664f);
    box-shadow: 0 0 20px rgb(3, 99, 78);
}
#equals{
    background:linear-gradient(135deg, #0f632b, #24a344);
    color: #fff;
}
#equals:hover{
    background:linear-gradient(135deg, #24a344, #0f632b);
    box-shadow: 0 0 20px rgb(12, 145, 63)
}
#add,
#subtract,
#multiply,
#divide{
    background:linear-gradient(135deg, #8bad2e, #4b6405);
    color: #fff;
}
#add:hover,
#subtract:hover,
#multiply:hover,
#divide:hover{
    background:linear-gradient(135deg, #97be09, #5ee742);
    box-shadow: 0 0 20px rgb(82, 131, 27)
}

#sin,
#cos,
#tan,
#log,
#ln,
#pi,
#e{
    background:linear-gradient(135deg, #ffe65a, #88670d);
    color: #fff;
}
#sin:hover,
#cos:hover,
#tan:hover,
#log:hover,
#ln:hover,
#pi:hover,
#e:hover{
    background:linear-gradient(135deg, #88670d, #ffe65a);
    box-shadow: 0 0 20px rgb(224, 145, 26)
}
#clear-history{
    background:linear-gradient(135deg, #ff8247, #883e0c);
    color: #fff;
    border: none;
    border-radius: 50px;
    box-shadow:0 0 10px rgb(104, 28, 18);
    transition: all 0.3s ease;
}
#clear-history:hover{
    background:linear-gradient(135deg, #883e0c, #ff8247);
    box-shadow: 0 0 20px rgb(124, 34, 22);
    transform: scale(1.1);
}
#clear-history:active{
    box-shadow: 0 0 10px rgb(104, 28, 18);
    transform: scale(0.95);
}
@media(max-width : 600px){
    .calculator{
        width: 95%;
        padding: 15px;
    }
    .buttons{
        gap: 10px;
    }
    button{
        height: 60px;
        font-size: 24px;
    }
}

@keyframes bounce{
    0%, 100%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-10px);
    }
}

button{
    animation: bounce 2s infinite ease-in-out;
}

.calculator::before{
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(135deg, rgba(255, 80, 176, 0.541), rgba(4, 213, 228, 0.425));
    animation: rotate-background 10s linear infinite;
    z-index: -1;

}

@keyframes rotate-background{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}